$black-color: #171718;
$black-border: #26292D;
$white: #fff;
$purple: #5C62EC;

$dark-bg: $black-color;
$dark-border: $black-border;
$header-text: $white;
$accent: $purple;
$text-color: $black-color;



.projects {
    padding: 70px 0;

    &__container {
        margin: 0 auto;
        max-width: 1200px;

    }

    &__title {
        color: $accent;
        font-size: 60px;
        line-height: 1.3;
        font-weight: 700;
        margin-bottom: 60px;
        text-align: center;

        @media (max-width:620px) {
            font-size: 40px;

        }

    }

    &__name {
        padding: 15px 20px 25px;
        font-size: 24px;
        font-weight: 700;
        line-height: 1.3;
    }

    &__lists {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        column-gap: 30px;
        row-gap: 30px;

        a {
            color: $black-color;
        }

    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        max-width: 370px;
        background-color: $white;
        box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);

        img {
            border-radius: 10px;
            width: 100%;

        }

        @media (max-width:400px) {
            max-width: 300px;
        }
    }


    @media (max-width:620px) {
        font-size: 40px;
        padding: 40px 0;
    }
}

.dark {
    box-shadow: 0px 5px 35px rgba(0, 0, 0, 1);
    background-color: #252526;

    h3 {
        color:white
    }
}